import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useCallback, useEffect, useState } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { Form, FormikProvider, useFormik } from 'formik';
// material
import { Box, Card, Grid, Stack, TextField, IconButton, InputAdornment, FormControlLabel, Switch } from '@mui/material';
import { DesktopDatePicker, LoadingButton } from '@mui/lab';
import { Icon } from '@iconify/react';
import eyeFill from '@iconify/icons-eva/eye-fill';
import closeFill from '@iconify/icons-eva/close-fill';
import eyeOffFill from '@iconify/icons-eva/eye-off-fill';
import Request from '../../../utils/Request';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
//
import Label from '../../Label';
import { UploadAvatar } from '../../upload';
import { useDispatch, useSelector } from '../../../redux/store';
import { getSurcussales, getSupervisors, getCountries, getCity } from '../../../redux/slices/user';
// ----------------------------------------------------------------------

SurcussaleNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentSurcussale: PropTypes.object
};

export default function SurcussaleNewForm({ isEdit, currentSurcussale }) {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const { surcussales, supervisors, countries, city } = useSelector((state) => state.user);

  const NewSurcussaleSchema = Yup.object().shape({
    addresse_complet: Yup.string().required('Addresse complet is required'),
    country_id: Yup.string().required('Country is required'),
    city_id: Yup.string().required('City is required'),
    supervisor_id: Yup.string().required('Supervisor is required')
  });

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      addresse_complet: currentSurcussale?.addresse_complet || null,
      country_id: currentSurcussale?.country || null,
      city_id: currentSurcussale?.city || null,
      supervisor_id: currentSurcussale?.supervisor || null,
      block: currentSurcussale?.block,
      surcussaleId: currentSurcussale?.id
    },
    validationSchema: NewSurcussaleSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      try {
        if (isEdit) {
          await Request.handleModifySurcussale(values);
        } else {
          await Request.handleAddSurcussale(values);
        }
        resetForm();
        setSubmitting(false);
        enqueueSnackbar(!isEdit ? 'Créer avec succès' : 'Mettre à jour avec succès', { variant: 'success' });
        getSurcussales();
        navigate(PATH_DASHBOARD.surcussale?.list);
      } catch (error) {
        if (error?.message) {
          enqueueSnackbar(error?.message, { variant: 'error' });
        } else {
          alert(JSON.stringify(error));
        }
        setSubmitting(false);
        setErrors(error);
      }
    }
  });

  useEffect(() => {
    dispatch(getSurcussales());
    dispatch(getSupervisors());
    dispatch(getCountries());
    dispatch(getCity());
  }, [dispatch]);

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={8}>
            <Card sx={{ p: 3 }}>
              <Stack spacing={3}>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    select
                    fullWidth
                    label="Pays"
                    placeholder="Choisir le Pays"
                    {...getFieldProps('country_id')}
                    SelectProps={{ native: true }}
                    error={Boolean(touched.country_id && errors.country_id)}
                    // helperText={touched.country_id && errors.country_id}
                  >
                    <option value="" />
                    {countries?.map((optio) => (
                      <option key={optio.id} value={optio.id}>
                        {optio.name}
                      </option>
                    ))}
                  </TextField>
                  <TextField
                    fullWidth
                    label="Ville"
                    placeholder="Choisir la ville"
                    {...getFieldProps('city_id')}
                    error={Boolean(touched.city_id && errors.city_id)}
                    // helperText={touched.city_id && errors.city_id}
                  />
                </Stack>

                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
                  <TextField
                    select
                    fullWidth
                    label="Superviseur"
                    placeholder="Choisir le superviseur"
                    {...getFieldProps('supervisor_id')}
                    SelectProps={{ native: true }}
                    error={Boolean(touched.supervisor_id && errors.supervisor_id)}
                    // helperText={touched.supervisor_id && errors.supervisor_id}
                  >
                    <option value="" />
                    {supervisors?.map((option) => (
                      <option key={option.id} value={option.id}>
                        {option.first_name} {option.last_name}
                      </option>
                    ))}
                  </TextField>
                  <TextField
                    fullWidth
                    label="Nom central"
                    placeholder="Entrer le nom du central"
                    {...getFieldProps('addresse_complet')}
                    error={Boolean(touched.addresse_complet && errors.addresse_complet)}
                    // helperText={touched.addresse_complet && errors.addresse_complet}
                  />
                </Stack>

                <Stack direction={{ xs: 'column', md: 'row' }} spacing={2}>
                  <FormControlLabel
                    control={
                      <Switch
                        {...getFieldProps('block')}
                        checked={values?.block}
                        color={!values?.block ? 'primary' : 'success'}
                      />
                    }
                    labelPlacement="start"
                    label="Block"
                  />
                </Stack>

                <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                  <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                    {!isEdit ? 'Créer Surcussale' : 'Sauvegarder les modifications'}
                  </LoadingButton>
                </Box>
              </Stack>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
