import { useEffect, useState } from 'react';
import { paramCase } from 'change-case';
import { useParams, useLocation } from 'react-router-dom';
// material
import { Container } from '@mui/material';
// redux
import { useDispatch, useSelector } from '../../redux/store';
import { getSurcussales } from '../../redux/slices/user';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import SurcussaleNewForm from '../../components/_dashboard/surcussale/SurcussaleNewForm';

// ----------------------------------------------------------------------

export default function SurcussaleCreate() {
  const { themeStretch } = useSettings();
  const dispatch = useDispatch();
  const { pathname } = useLocation();
  const { surcussaleId } = useParams();
  const { surcussales } = useSelector((state) => state.user);
  const isEdit = pathname.includes('edit');
  const currentSurcussale = surcussales.find((superviso) => superviso?.id === Number(surcussaleId));
  const [currentSurcussaleLoaded, setCurrentSurcussaleLoaded] = useState(false);
  useEffect(() => {
    if (currentSurcussale) {
      setCurrentSurcussaleLoaded(true);
    }
  }, [currentSurcussale]);

  useEffect(() => {
    // Load data on the initial render
    dispatch(getSurcussales());
  }, []);

  return (
    <Page title="Surcussale: Créer un nouvel surcussale | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={!isEdit ? 'Créer un nouvel surcussale' : 'Modifier Surcussale'}
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Surcussale', href: PATH_DASHBOARD.surcussale?.root },
            { name: !isEdit ? 'Nouvel surcussale' : 'Modification' }
          ]}
        />
        {(!isEdit || currentSurcussaleLoaded) && (
          <SurcussaleNewForm isEdit={isEdit} currentSurcussale={currentSurcussale} />
        )}
      </Container>
    </Page>
  );
}
